import {IndexModel} from '@/model/index'
const indexModel = new IndexModel()
export default {
    props:{
        showPopup:{
            type : Boolean,
            default : false
        },
    },
    data() {
        return {
            // 店铺列表
            storeList: [
                {
                    id: 1,
                    name: '成华区-浅水半岛店'
                },
                {
                    id: 2,
                    name: '新都区-柏水店'
                },
                {
                    id: 3,
                    name: '郫都区-鹃兴路店'
                },
                {
                    id: 4,
                    name: '郫都区-鹃兴路店'
                },
                {
                    id: 5,
                    name: '郫都区-鹃兴路店'
                },
            ],
            // 选中的店铺
            selectedStore: {},
            color: '#BEBEBE',
            showMask:false,
            shops:[]
        }
    },
    methods: {
        check(e) {
            console.log(e)
            this.selectedStore = {};
            let id = e.currentTarget.dataset.id;
            this.shops.forEach(item => {
                if(item.id == id) {
                    this.selectedStore = item;
                }
            });
            console.log(this.selectedStore)
        },
        close()
        {
            this.showMask = false;
            this.$emit('close');
        },
        save()
        {
            console.log(this.selectedStore)
            this.showMask = false;
            this.$emit('save',this.selectedStore);
        },
        getShop()
        {
            indexModel.getShopList().then((res) => {
                this.shops = res.data
            })
        }
    },
    mounted(){
        this.showMask = this.showPopup
        this.getShop()
    }
}
